.dots-header {
  width: 100%;
  color: white;
  font-size: 1.5em;
}
.dots-header .dots-header--title {
  width: 100%;
  padding: 10px 0;
  background-color: #447044;
  margin-bottom: 3px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.dots-header .dots-header--border {
  width: 100%;
  padding: 3.5px 0;
  border-top: 2px solid #447044;
  border-bottom: 2px solid #447044;
  -webkit-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.dots-header--ingame {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 20px;
  color: white;
  -webkit-box-shadow: 0px 6px 15px 4px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 6px 15px 4px rgba(0, 0, 0, 0.75);
}



