.dots-game-container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  background-color: #3b3f47;
}
.dots-game-container--done {
  background-color: white;
  opacity: 1;
}
.dots-game-container--done .dots-btn {
  border: 2px solid #447044;
}
.dots-game-container--done .dots-btn--inner {
  background-color: #447044;
}
.dots-game-container--done .dots-btn--active {
  border: 2px solid #447044;
}
.dots-game-container--done .dots-btn--inner-active {
  background-color: #447044;
}



